/* Component Styles */

:root {
  --font-family-sans: 'Gordita';
  --font-family-sans-fallback: 'Helvetica Neue', Arial, sans-serif;
  --font-stack-sans: var(--font-family-sans), var(--font-family-sans-fallback);
  
  --brand-blue: #1c49c2;
}

html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}

body {
  background-color: #ffffff;
  display: flex;
  font-family: var(--font-stack-sans);
  height: 100vh;
  margin: 0;
  padding: 0;
}

picture {
  margin: 0;
  padding: 0;
  max-width: 100vw;
}

img {
  height: auto;
  max-width: 100%;
  width: 100%;
}

button {
  font-family: var(--font-stack-sans);
  background-color: var(--brand-blue);
  border-radius: 1rem;
  border: none;
  font-weight: 500;
  font-size: clamp(0.875rem, 1vw + 0.5rem, 1rem);
  color: #ffffff;
  padding: 0.25rem 1rem 0.3rem 1rem;
}

.component {
  justify-content: center;
  border: solid 1px green;
  display: flex;
  height: 100%;
  position: relative;
  width: 100%;
}

.component-image {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  max-width: 100%;
  width: 100%;
  z-index: -1;
  img {
    height: 100%;
    object-fit: cover;
    object-position: center 20%;
    @media screen and (min-width: 40em) {
          object-position: center center;
    }
  }
}

.text-cta-container {
  //border: solid 1px red;
  display: flex;
  height: 100%;
  justify-content: flex-end;
  align-items: center;
  max-width: 35rem;
  margin-left: auto;
  margin-right: auto;
  flex-direction: column;
  padding: 1rem 0.5rem;
  text-align: center;
    @media screen and (min-width: 40em) {
      justify-content: center;
    }
  .block-left & {
    margin-left: 5vw;
  }
  .block-right & {
    margin-right: 5vw;
  }
  .text-left & {
    align-items: flex-start;
    text-align: left;
  }
  .text-right & {
    align-items: flex-end;
    text-align: right;
  }
  .align-top & {
    @media screen and (min-width: 40em) {
      justify-content: start;
    }
  }
  .align-bottom & {
    justify-content: flex-end;
  }
  > {
    * {
      transition: all 0.2s ease-in-out;
    }
    div + div {
      margin-top: 0.5rem;
    }
  }
}

.ctas {
  margin-top: 0.75rem;
}

[contenteditable="true"] {
  &:focus {
    outline: solid 1px black;
    //outline-offset: 0.25rem;
    background-color: rgba(255,255,255,0.25);
    color: #000000;
  }
  &.hidden {
    display: none;
  }
}

.hidden {
  display: none;
}

/* Text styles */
.level-7 {
  font-weight: normal;
  font-size: clamp(1.75rem, calc(3vw + 1rem), 3.75rem);
  line-height: 1.01;
  &.bold, 
  .bold,
  b, 
  strong {
    font-weight: bold;
  }
  &.black,
  .black {
    font-weight: 900;
  }
}

